import React from 'react';

import styles from './style.scss';

const Thumbnail = props => {
  const { title, period, details, companyURL } = props;

  return (
    <div className={styles.experience_wrapper}>
      <div className={styles.experience_headings}>
        <a href={companyURL}>
          <div className={styles.experience_title}>{title}</div>
        </a>
        <div className={styles.experience_period}>{period}</div>
      </div>
      <div className={styles.experience_details}>
        {details.map(detail => (
          <div className={styles.experience_detail}> ✔️ {detail} </div>
        ))}
      </div>
    </div>
  );
};

export default Thumbnail;
